
import XHeader from 'vux/src/components/x-header/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'

import Scroll from 'assembly/scroll/scroll'

export default {
data () {
return {
id: '',
listenScroll: true,
type: '',
title: '',
detail: {}
}
},
methods: {
_getDetail () {
this.$tupHttp(this, 'front/report/detail?id=' + this.id).then(res => {
this.detail = res.data.model
this.title = Number(this.type) === 1 ? this.detail.year + '/' + this.detail.month + ' 第' + this.detail.week + '周 周报' : this.detail.year + '/' + this.detail.month + ' 月报'
})
}
},
created () {
this.id = this.$route.query.id
this.type = this.$route.query.type
console.log(this.type)
if (this.id) {
this._getDetail()
}
},
components: {
XHeader,
Cell,
Group,
XTextarea,
Scroll
}
}



